export const QUEUE_KEY = 'lochusa:queue'

export function pushToQueue(payload) {
	const q = JSON.parse(localStorage.getItem(QUEUE_KEY) || '[]')
	q.push(payload)
	localStorage.setItem(QUEUE_KEY, JSON.stringify(q))
}

export async function syncQueue() {
	if (!navigator.onLine) return
	const q = JSON.parse(localStorage.getItem(QUEUE_KEY) || '[]')
	if (!q.length) return

	for (const item of q) {
		try {
			await fetch('http://localhost:8080/guardar-tarea', {
				method: 'POST',
				headers: { 'Content-Type': 'application/json' },
				body: JSON.stringify(item)
			})
		} catch (err) {
			console.warn('sync failed for item', item)
			return // abortamos: quedará para el siguiente intento
		}
	}
	localStorage.removeItem(QUEUE_KEY)
	console.log('Queue synced')
}
