<template>
  <div id="app">
    <h1>Gestión de Pipsas</h1>

    <!-- Barra de Tabs -->
    <div class="tabs">
      <span
        v-for="(tab, index) in tabs"
        :key="index"
        :class="{ active: index === paso }"
      >
        {{ tab.titulo }}
      </span>
    </div>

    <!-- Contenido del tab -->
    <TodoList
      v-for="(tab, index) in tabs"
      v-show="index === paso"
      :key="index"
      :titulo="tab.titulo"
      :placeholder="tab.placeholder"
      v-model:items="tab.items"
      :tabIndex="index"
    />

    <!-- Barra inferior con progreso y botones -->
    <div class="barra-bottom">
      <div class="progress-bar">
        <div class="progress" :style="{ width: progreso + '%', background: gradiente }"></div>
      </div>
      <div class="botones">
        <button @click="prev" :disabled="paso === 0">⬅ Prev</button>
        <button @click="next" :disabled="paso === tabs.length - 1">Next ➡</button>
      </div>
    </div>
  </div>
</template>

<script>
import TodoList from './components/TodoList.vue'

export default {
  name: 'App',
  components: { TodoList },
  data() {
    return {
      paso: 0,
      tabs: [
        { titulo: 'Pipsas', placeholder: 'pipsa', items: [{ nombre: 'Margarita' }, { nombre: 'Pepperoni' }] },
        { titulo: 'Ingredientes', placeholder: 'ingrediente', items: [{ nombre: 'Queso' }, { nombre: 'Jamón' }] },
        { titulo: 'Paquetes', placeholder: 'paquete', items: [] },
        { titulo: 'Extras', placeholder: 'extra', items: [{ nombre: 'Refresco' }, { nombre: 'Papas' }, { nombre: 'Postre' }] }
      ]
    }
  },
  computed: {
    progreso() {
      return ((this.paso + 1) / this.tabs.length) * 100
    },
    gradiente() {
      const total = this.tabs.length
      if (this.paso === 0) return '#42b983'
      if (this.paso === total - 1) return '#e74c3c'
      return '#f1c40f'
    }
  },
  methods: {
    next() {
      if (this.paso < this.tabs.length - 1) this.paso++
    },
    prev() {
      if (this.paso > 0) this.paso--
    }
  }
}
</script>

<style>
#app {
  max-width: 95%;
  margin: 1rem auto;
  background: #fff;
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);
  display: flex;
  flex-direction: column;
  min-height: 80vh;
}

h1 {
  text-align: center;
  margin-bottom: 1rem;
  font-family: 'Segoe UI', sans-serif;
  font-size: 1.5rem;
}

/* Tabs responsive estilo Notepad++ */
.tabs {
  display: flex;
  overflow: hidden;
  gap: 0.3rem;
  background: #f5f5f5;
  border-radius: 8px;
}

.tabs span {
  flex: 1 1 0; /* todas ocupan igual */
  padding: 0.5rem 0.8rem;
  border-radius: 6px;
  background: #ddd;
  color: #666;
  font-weight: bold;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; /* trunca el texto */
  text-align: center;
  transition: all 0.3s ease;
}

.tabs span.active {
  background: #42b983;
  color: white;
  flex: 0 0 auto; /* tamaño auto para mostrar completo */
  white-space: normal; /* título completo */
  padding: 0.5rem 1rem;
}

/* Barra inferior */
.barra-bottom {
  margin-top: auto;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.progress-bar {
  width: 100%;
  height: 14px;
  background: #eee;
  border-radius: 7px;
  overflow: hidden;
}

.progress {
  height: 100%;
  transition: width 0.5s ease, background 0.5s ease;
}

.botones {
  display: flex;
  justify-content: space-between;
}

.botones button {
  flex: 1;
  margin: 0 0.2rem;
  padding: 0.5rem;
  border: none;
  border-radius: 8px;
  background: #42b983;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.botones button:hover:not(:disabled) {
  background: #369f6e;
  transform: scale(1.05);
}

.botones button:disabled {
  background: #ccc;
  cursor: not-allowed;
}

/* Mobile */
@media (max-width: 600px) {
  .barra-bottom .botones {
    flex-direction: column;
    gap: 0.3rem;
  }
}
</style>
