<template>
  <div class="todo-list">
    <h2>{{ titulo }}</h2>
    
    <div class="agregar">
      <input
        v-model="nuevaItem"
        :placeholder="'Agregar ' + placeholder"
        @keyup.enter="agregar"
      />
      <button @click="agregar">Agregar</button>
    </div>

    <ul>
      <li v-for="(item, index) in items" :key="index">
        {{ item.nombre }}
        <button class="eliminar" @click="eliminar(index)">✖</button>
      </li>
    </ul>

    <button class="guardar" @click="guardarServidor">Guardar</button>
  </div>
</template>

<script>

import { pushToQueue } from '@/utils/offlineQueue'

export default {
  name: "TodoList",
  props: {
    items: Array,
    titulo: String,
    placeholder: String,
    tabIndex: Number
  },
  data() {
    return { nuevaItem: '' }
  },
  methods: {
    agregar() {
      if (!this.nuevaItem.trim()) return
      const nuevaLista = [...this.items, { nombre: this.nuevaItem }]
      this.$emit('update:items', nuevaLista)
      this.nuevaItem = ''
    },
    eliminar(index) {
      const nuevaLista = this.items.filter((_, i) => i !== index)
      this.$emit('update:items', nuevaLista)
    },

		async guardarServidor(tabIndex) {
		const payload = { tab: tabIndex, items: this.items, ts: Date.now() }
		if (!navigator.onLine) {
			pushToQueue(payload)
			alert('Sin conexión: se guardó localmente y se sincronizará al reconectar.')
			return
		}
		// si hay conexión
		const res = await fetch('http://pp-pippsa.test:8383/', {
			method: 'POST',
			headers: { 'Content-Type': 'application/json' },
			body: JSON.stringify(payload)
		})
		const data = await res.json()
		alert('Guardado: ' + JSON.stringify(data))
	}


  }
}
</script>

<style>
.todo-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin: 0.5rem 0;
}

.agregar {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.agregar input {
  flex: 1;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
}

.agregar button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 6px;
  background: #42b983;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

.agregar button:hover { background: #369f6e; }

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

li {
  display: flex;
  justify-content: space-between;
  padding: 0.4rem 0.6rem;
  border-bottom: 1px solid #eee;
}

.eliminar {
  border: none;
  background: #e74c3c;
  color: #fff;
  padding: 0.2rem 0.6rem;
  border-radius: 6px;
  cursor: pointer;
}

.eliminar:hover { background: #c0392b; }

.guardar {
  margin-top: 0.5rem;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 6px;
  background: #3498db;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
}

.guardar:hover { background: #2980b9; }

@media (max-width: 600px) {
  .agregar { flex-direction: column; }
  .agregar input, .agregar button, .guardar { width: 100%; }
  li { flex-direction: column; align-items: flex-start; }
  .eliminar { margin-top: 0.3rem; }
}
</style>
